import 'assets/plugins/nucleo/css/nucleo.css'
import '@fortawesome/fontawesome-free/css/all.min.css'
import 'assets/scss/argon-dashboard-react.scss'
import 'assets/css/style.css'
//import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useState } from 'react'
import { REMOVE_ERROR } from './constants/alertConstant'
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom'
import AdminLayout from 'layouts/Admin.js'
import AuthLayout from 'layouts/Auth.js'
import { useDispatch, useSelector } from 'react-redux'
import Home from 'layouts/Home.js'
import ResetPassword from 'views/examples/resetPassword.js'
import ForgotPassword from 'views/examples/ForgotPassword.js'

import { loadUser } from './actions/userActions'
import ReactJkMusicPlayer from 'react-jinke-music-player'
import 'react-jinke-music-player/assets/index.css'
import store from './store'
import { useToasts } from 'react-toast-notifications'
import { REMOVE_MUSIC } from 'constants/playlistConstant'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

function App() {
  const dispatch = useDispatch()
  const [audioLists, setAudioLists] = useState([
    {
      name: 'weeknd',
      cover:
        'data:image/png;base64,',
      musicSrc: 'http://localhost:8888/js?vid=XXYlFuWEuKI',
    },
    { musicSrc: './XXYlFuWEuKI.mp3' },
  ])
  const { error } = useSelector((state) => state.alerts)
  const { isAuthenticated, loading } = useSelector((state) => state.auth)

  const { playlist, error: Musicerror, loading: loadingMusic } = useSelector(
    (state) => state.musicList
  )
  const { addToast } = useToasts()
  React.useEffect(() => {
    if (!isAuthenticated) store.dispatch(loadUser())
    if (error) {
      addToast(error.message, { appearance: error.type })
      dispatch({ type: REMOVE_ERROR })
    }
  }, [error])



  return (
    <>

      {loading == false ? (
        <BrowserRouter>
          <Switch>
            <Route path="/admin" render={(props) => <AdminLayout {...props} />} />
            <Route path="/auth" render={(props) => <AuthLayout {...props} />} />
            <Route path="/home" render={(props) => <Home {...props} />} />
            <Route path="/resetPassword/:token" render={(props) => <ResetPassword {...props} />} />
            <Redirect from="/" to="/admin/index" />
          </Switch>
          <ToastContainer />
        </BrowserRouter>) : (<div className="loader">
          <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={3000} //3 secs
          />

      </div>)}
      {isAuthenticated && (
        <ReactJkMusicPlayer
          quietUpdate
          onAudioListsChange={(currentPlayId, audioLists, audioInfo) => {
            dispatch({ type: REMOVE_MUSIC, payload: audioLists })
          }}
          remove={true}
          showDownload={false}
          autoPlay={false}
          mode='mini'
          theme='light'
          clearPriorAudioLists
          audioLists={playlist}
        />
      )}
    </>
  )

}

export default App
