import React ,{useState,useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
// reactstrap components
import { Container } from "reactstrap";
// core components
import AdminNavbar from "components/Navbars/AdminNavbar.js";
import AdminFooter from "components/Footers/AdminFooter.js";
import Sidebar from "components/Sidebar/Sidebar.js";
/* import { loadUser } from '../actions/userActions'
import store from '../store' */

import routes,{notLoggedRoutes,managerRoutes,agentRoutes} from "routes.js";

const Admin = (props) => {
  const mainContent = React.useRef(null);
  const location = useLocation();

  const  {isAuthenticated,error,loading,user} = useSelector(state => state.auth)
  const dispatch = useDispatch()

  /* React.useEffect(() => {
    store.dispatch(loadUser())
  }, []);
 */
  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainContent.current.scrollTop = 0;
  }, [location]);


  let routesSelected

  //manager route
  if(user && user.admin)
  {
    routesSelected = managerRoutes;

  }
//agent route
  if(user && !user.admin)
  {
    routesSelected = agentRoutes;

  }

  //not logged it route
  if(!user){
    routesSelected = notLoggedRoutes
  }
  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/admin") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  const getBrandText = (path) => {
    for (let i = 0; i < routes.length; i++) {
      if (
        props.location.pathname.indexOf(routes[i].layout + routes[i].path) !==
        -1
      ) {
        return routes[i].name;
      }
    }
    return "Brand";
  };

  return (
    <>
      <Sidebar
        {...props}
        routes={routesSelected}
        logo={{
          innerLink: "/admin/index",
          imgSrc: require("../assets/img/HR.png").default,
          imgAlt: "...",
        }}
      />
      <div className="main-content" ref={mainContent}>

      {user && <AdminNavbar
          {...props}
          brandText={getBrandText(props.location.pathname)}
        />}
        
        <Switch>
          {getRoutes(routesSelected)}
          <Redirect from="*" to="/admin/index" />
        </Switch>
        <Container fluid>
          <AdminFooter />
        </Container>
      </div>
    </>
  );
};

export default Admin;
